			<div class="box">
				<img src="./assets/img/images/7.jpg" alt="">
			</div>
		</div>
	</div>
</template>

<script>
export default {

  name: 'waterfall',

  data () {
    return {

    };
  }
};
</script>

<style lang="scss" scoped>
#waterfall{
	width:100%;
	background:rgb(245,245,245);
	float:left;
	padding-bottom:15px;
	@at-root .column{
		display: inline-block;
		width: calc(25% - 17.5px);
		margin-left: 15px;

		@at-root .box{
			position:relative;
			
			
			margin-top: 15px;
			height: 500px;
			background:white;
			box-shadow: 0 0 5px #aaa;
			transition: all ease 0.3s;

			&:hover{
				box-shadow: 0 0 15px 3px #aaa;
				transform: scale(1.02);
			}
		}
	}
	
}
</style>


